elle Bonnard (architecte DPLG), Fabienne Desnoual (chercheuse en design), J-P. Grunfeld (sémiologue urbain), Pierre-Damien Huyghe (philosophe) et Jacques Prades (économiste)."),_c('br'),_vm._v(" — Workshop animé par Charlotte Talbot (designer) du 26 au 30 novembre 2018. L’axe de travail sera ici de penser des propositions de principes et de systèmes déclinables, adaptables et appropriables par les futurs utilisateurs du supermarché."),_c('br'),_vm._v(" — Restitution du travail devant les membres du supermarché coopératif de la chouette coop. ")])])]),_c('div',{staticClass:"isdat-figures"},[_c('div',{staticClass:"isdat-figure"},[_c('figcaption',[_vm._v(" Recherche de représentation de différents modes de culture ")]),_c('div',{staticClass:"isdat-figure-element"},[_c('img',{attrs:{"src":"/isdat-mn/images/02.jpg","alt":""}})])]),_c('div',{staticClass:"isdat-figure"},[_c('figcaption',[_vm._v(" Maquette de principe pour l'aménagement d'un supermarché fondé sur la coopération ")]),_c('div',{staticClass:"isdat-figure-element"},[_c('img',{attrs:{"src":"/isdat-mn/images/03.jpg","alt":""}})])])]),_c('div',{staticClass:"isdat-page-cols nomargin"},[_c('div',{staticClass:"isdat-page-left"}),_c('div',{staticClass:"isdat-page-right"},[_c('hr'),_c('h4',{staticClass:"isdat-mono-title"},[_vm._v("Responsables du projet")]),_c('ul',{staticClass:"isdat-mono-list"},[_c('li',[_c('strong',[_vm._v("[2017-2018]")]),_vm._v(" Laetitia Giorgino | Hanika Perez.")]),_c('li',[_c('strong',[_vm._v("[2018-2019]")]),_vm._v(" Laetitia Giorgino | Romain-Paul Lefevre | Hanika Perez.")])]),_c('h4',{staticClass:"isdat-mono-title"},[_vm._v("Étudiants associés")]),_c('ul',{staticClass:"isdat-mono-list"},[_c('li',[_c('strong',[_vm._v("[2017-2018]")]),_vm._v(" Henda Ali | Marlène Bernabé | Marine D’agostinis | Vincent Kremer | Gala Lorfeuvre | Yuanxiao Li | Mengwen Yang ")]),_c('li',[_c('strong',[_vm._v("[2018-2019]")]),_vm._v(" May Antoine | Johan Armand | Alessia Cosendai | Justine Gigot | Julie Berthaud | M